import * as React from "react";
import { useState,useRef,useEffect } from "react";
import { Button } from "../Button/Button";
import "../../resources/fontello-7bec632c/css/fontello.css";
import styles from "./GifMaker.scss";
import { RightPanel } from "./RightPanel/RightPanel";
import { createDescriptor, TAnimationDefinition, descriptorToSvg } from "@prethora/alchemy_labs_logo_svg_demo_animation_manager";
import { Tabs } from "../Tabs/Tabs";
import { originalBeaker, originalCommons, originalRight, originalUnder } from "../../resources/original_configs";
import { saveFile, saveUrl } from "../../saveurl";
import { getTimestamp } from "../../get_timestamp";
import { ErrorMessage, useErrorMessage } from "./RightPanel/ErrorMessage/ErrorMessage";
import { createDefinition } from "../../create_definition";
import { Background } from "./RightPanel/Background/Background";
import { loadInitialConfig, saveConfig } from "../../local_storage";
import { IconButton } from "../IconButton/IconButton";
import { handler } from "../../helpers";

export type TLogoType = "under" | "right" | "beaker";

export function GifMaker()
{    
    const [selectedTabIndex,setSelectedTabIndex] = useState(0);
    const [ready,setReady] = useState(false);
    const [svgUrl,setSvgUrl] = useState<string | undefined>();

    const sourceSvgRef = useRef("");
    const initEventCounterRef = useRef(0);

    const textRef = useRef<HTMLTextAreaElement>(null);
    const commonsRef = useRef<HTMLTextAreaElement>(null);

    const incInitEvent = () => 
    {
        initEventCounterRef.current++;
        if (initEventCounterRef.current===2)
        {
            setReady(true);
        }
    };

    const spinner = (
        <svg className={styles.spinner} viewBox="0 0 50 50">
            <circle className={styles.path} cx="25" cy="25" r="20" fill="none" strokeWidth="5"></circle>
        </svg>
    );

    const selectHandler = (index: number) => 
    {
        if (index!==permRef.current.selectedTabIndex)
        {
            setSelectedTabIndex(index);
            closeErrorMessage();
        }        
    };

    const [commons,setCommons] = useState("");
    const [under,setUnder] = useState("");
    const [right,setRight] = useState("");
    const [beaker,setBeaker] = useState("");

    useEffect(() => 
    {
        const { under,right,beaker,commons } = loadInitialConfig();
        setUnder(under);
        setRight(right);
        setBeaker(beaker);
        setCommons(commons);
    },[]);

    const lastValidConfigRef = useRef({under: "",right: "",beaker: "",commons: ""});

    const [underUrl,setUnderUrl] = useState<string | undefined>();
    const [rightUrl,setRightUrl] = useState<string | undefined>();
    const [beakerUrl,setBeakerUrl] = useState<string | undefined>();

    const setTypeUrl = (type: TLogoType,url: string) => 
    {
        if (type==="under") setUnderUrl(url);
        if (type==="right") setRightUrl(url);
        if (type==="beaker") setBeakerUrl(url);
    };

    const permRef = useRef({commons,under,right,beaker,selectedTabIndex,latestBackgroundColor: "transparent"});
    permRef.current.commons = commons;
    permRef.current.under = under;
    permRef.current.right = right;
    permRef.current.beaker = beaker;
    permRef.current.selectedTabIndex = selectedTabIndex;
    const latestBackgroundColor = permRef.current.latestBackgroundColor;

    const lastValidHeightsRef = useRef({under: 0,right: 0,beaker: 0});

    const getConfigByType = (type: TLogoType) => 
    {
        const { under,right,beaker } = permRef.current;
        if (type==="under") return under;
        if (type==="right") return right;
        if (type==="beaker") return beaker;
        return "";
    };

    const getCurrentUrl = () => 
    {
        const currentType = getCurrentType();
        if (currentType==="under") return underUrl;
        if (currentType==="right") return rightUrl;
        if (currentType==="beaker") return beakerUrl;
        return "";
    };

    const isFirstTimeRef = useRef(true);

    useEffect(() =>
    {
        const { under,right,beaker,commons } = permRef.current;
        const lastValidConfig = lastValidConfigRef.current;
        if ((under!=="") && (right!=="") && (beaker!=="") && (commons!=="") && (isFirstTimeRef.current))
        {
            isFirstTimeRef.current = false;
            (async () => 
            {
                const response = await fetch("/resources/source.svg");
                sourceSvgRef.current = await response.text();
        
                (["under","right","beaker"] as TLogoType[]).forEach((type) => 
                {                            
                    const configByType = getConfigByType(type);
                    const definition = createDefinition(configByType,commons,type);                
                    if (typeof definition==="string")
                    {
                        alert(`unexpected error: initial configuration for type "${type}" is invalid: `+definition);
                        return;
                    }
                    permRef.current.latestBackgroundColor = definition.backgroundColor;
                    const descriptor = createDescriptor(definition);
                    const svgContent = descriptorToSvg(descriptor,sourceSvgRef.current);
        
                    const blob = new Blob([svgContent],{type: "image/svg+xml"});
                    const url = URL.createObjectURL(blob);
                    setTypeUrl(type,url);
                    lastValidConfig[type] = configByType;
                    lastValidConfig.commons = commons;
                });
                incInitEvent();
            })();
            setTimeout(incInitEvent,1700);    
        }
    });

    const getCurrentConfig = () => 
    {
        const { selectedTabIndex,under,right,beaker } = permRef.current;
        return (selectedTabIndex===0)?under:((selectedTabIndex===1)?right:beaker);
    };

    const getCurrentType = (): TLogoType => 
    {
        const { selectedTabIndex } = permRef.current;
        return (selectedTabIndex===0)?"under":((selectedTabIndex===1)?"right":"beaker");
    };

    const currentType = getCurrentType();

    const mainConfig = (() => 
    {
        if (selectedTabIndex===0) return under;
        if (selectedTabIndex===1) return right;
        if (selectedTabIndex===2) return beaker;
        return "";
    })();
    const updateConfig = (text: string) => 
    {
        if (selectedTabIndex===0) 
        {
            setUnder(text);
            updatePreviewable("under",true);  
        }
        if (selectedTabIndex===1) 
        {
            setRight(text);
            updatePreviewable("right",true);  
        }
        if (selectedTabIndex===2) 
        {
            setBeaker(text);
            updatePreviewable("beaker",true);  
        }
        closeErrorMessage();
    };
    const updateCommons = (text: string) => 
    {
        setCommons(text);
        updatePreviewable("commons",true);  
        closeErrorMessage();
    };

    const [previewable,setPreviewable] = useState({under: false,right: false,beaker: false,commons: false});
    const previewableRef = useRef(previewable);
    previewableRef.current = previewable;

    const updatePreviewable = (type: TLogoType | "commons",value: boolean) => 
    {
        const previewable = {...previewableRef.current};
        previewable[type] = value;
        setPreviewable(previewable);
        previewableRef.current = previewable;
    };

    const { props: errorMessageProps,showErrorMessage,closeErrorMessage  } = useErrorMessage();

    const previewClickHandler = (_configs?: {under: string,right: string,beaker: string,commons: string},bypassPreviewable = false) =>
    {
        const lastValidConfig = lastValidConfigRef.current;
        const previewable = bypassPreviewable?{under: true,right: true,beaker: true,commons: true}:previewableRef.current;
        // const { commons } = permRef.current;
        const currentType = getCurrentType();

        const configs = _configs || {
            under: permRef.current.under,
            right: permRef.current.right,
            beaker: permRef.current.beaker,
            commons: permRef.current.commons
        };

        let errorFound = false;        

        (["under","right","beaker"] as TLogoType[]).forEach((type) => 
        {
            if (errorFound) return;
            if (((currentType===type) && previewable[type]) || previewable.commons)
            {                
                const configByType = configs[type];
                const definition = createDefinition(configByType,configs.commons,type);
                if (typeof definition==="string")
                {
                    showErrorMessage(definition);
                    errorFound = true;
                    return;
                }
                permRef.current.latestBackgroundColor = definition.backgroundColor;
                const descriptor = createDescriptor(definition);
                const svgContent = descriptorToSvg(descriptor,sourceSvgRef.current);
    
                const blob = new Blob([svgContent],{type: "image/svg+xml"});
                const url = URL.createObjectURL(blob);
                setTypeUrl(type,url);
                updatePreviewable(type,false);
                saveConfig(type,configByType);
                saveConfig("commons",configs.commons);
                lastValidConfig[type] = configByType;
                lastValidConfig.commons = configs.commons;
            }
        });
        if (!errorFound) updatePreviewable("commons",false);
    };

    const getCurrentResolutionHeight = () => 
    {
        const { commons } = permRef.current;
        const type = getCurrentType();
        return lastValidHeightsRef.current[type];
    };

    const getCurrentFileName = () => 
    {
        const height = getCurrentResolutionHeight();
        const type = getCurrentType();
        const timestamp = getTimestamp();
        return `alchemy-labs-logo-${type}-h${height}-${timestamp}.svg`;
    };

    const getCurrentExportFileName = () => 
    {
        const timestamp = getTimestamp();
        return `alchemy-labs-logo-config-${timestamp}.json`;
    };

    const downloadSvgClickHandler = () => 
    {        
        const url = getCurrentUrl();
        const fileName = getCurrentFileName();
        if (url) saveUrl(url,fileName);
    };

    const setAllConfig = (config: {under: string,right: string,beaker: string,commons: string}) => 
    {
        previewClickHandler(config,true);
        setUnder(config.under);
        setRight(config.right);
        setBeaker(config.beaker);
        setCommons(config.commons);
    };

    const resetHandler = () => 
    {        
        if (confirm("Are you sure you want to reset to the default configuration?"))
        {
            setAllConfig({under: originalUnder,right: originalRight,beaker: originalBeaker,commons: originalCommons});
        }
    };

    const exportHandler = () => 
    {
        const fileName = getCurrentExportFileName();
        const { under,right,beaker,commons } = lastValidConfigRef.current;
        const payload = JSON.stringify({ under,right,beaker,commons },null,2);
        saveFile(payload,"application/json",fileName);
    };

    const previewDisabled = !previewable[currentType] && !previewable.commons;

    const inputFileRef = useRef<HTMLInputElement>(null);

    const parseJson = (text: string) => 
    {
        try
        {
            return JSON.parse(text);
        }
        catch(err)
        {
            return null;
        }
    };

    const fileChangeHandler = (file: File) => 
    {        
        const reader = new FileReader();
        reader.onload = function()
        {
            const text = reader.result as string;
            const res = parseJson(text) as {under: string,right: string,beaker: string,commons: string};
            if ((res!==null) && (typeof res.under==="string") && (typeof res.right==="string") && (typeof res.commons==="string"))
            {                
                const commons = res.commons;
                let errorFound = false;
                (["under","right","beaker"] as TLogoType[]).forEach((type) => 
                {
                    if (errorFound) return;
                    const definition = createDefinition(res[type],commons,type);
                    if (typeof definition==="string")
                    {
                        errorFound = true;
                    }    
                });
                if (!errorFound)
                {
                    setAllConfig(res);
                    return;
                }
            }
            showErrorMessage("Imported config file is invalid",5000);
        };
        reader.readAsText(file);        
        if (inputFileRef.current) inputFileRef.current.value = "";
    };

    const importHandler = () => 
    {
        if (inputFileRef.current)
        {
            inputFileRef.current.click();
        }
    };
    
    const app = (
        <div className={styles.container}>
            <input className={styles.inputFile} ref={inputFileRef} type="file" accept="application/json" onChange={(event) => { event.target.files?fileChangeHandler(event.target.files[0]):null }} />
            <div className={styles.left}>
                <Tabs selectedIndex={selectedTabIndex} onSelect={selectHandler} />
                <div className={styles.body}>
                    <textarea 
                        ref={textRef} 
                        onChange={() => updateConfig(textRef.current!.value)}
                        className={styles.main} 
                        value={mainConfig} 
                        spellCheck={false} />
                    <div className={styles.commonsTitle}>Commons</div>
                    <textarea 
                        ref={commonsRef} 
                        onChange={() => updateCommons(commonsRef.current!.value)} 
                        className={styles.commons} 
                        value={commons} 
                        spellCheck={false} />
                    <div className={styles.buttons}>
                        <div className="flex-row-between">
                            <IconButton icon="eye" text="Preview" disabled={previewDisabled} onClick={previewClickHandler} />
                            <IconButton icon="down-circled2" text="SVG" onClick={downloadSvgClickHandler} />
                            <IconButton icon="down-circled2" text="GIF" disabled />
                        </div>
                        <div className="flex-row-between padding-top-10">
                            <IconButton icon="arrows-cw" text="Reset" onClick={resetHandler} />
                            <IconButton icon="logout" text="Export" onClick={exportHandler} />
                            <IconButton icon="login" text="Import" onClick={importHandler} />
                        </div>
                    </div>
                </div>
            </div>
            <div className={styles.right}>
                <ErrorMessage {...errorMessageProps} />
                <RightPanel urls={{under: underUrl,right: rightUrl,beaker: beakerUrl}} type={currentType} />
                <Background color={latestBackgroundColor} />
            </div>
        </div>
    );

    return (<div className={styles.controller+" "+(ready?styles.ready:"")}>
        {app}{spinner}
    </div>);
}
