import * as React from "react";
import { useState } from "react";
import { styler,handler } from "../../helpers";
import _styles from "./IconButton.scss";
const s = styler(_styles as any);

interface TProp
{
    icon: string;
    text: string;
    disabled?: boolean;
    onClick?: () => void;
}

export function IconButton({icon,text,disabled,onClick}: TProp)
{    
    return (
        <div className={s("container",["disabled",disabled])} onClick={handler(onClick)}>
            <div className={s("icon")}>
                <i className={"icon-"+icon} />
            </div>
            <div className={s("text")}>
                {text}
            </div>            
        </div>
    );
}
