import { TLogoType } from "./components/GifMaker/GifMaker";
import { originalBeaker, originalCommons, originalRight, originalUnder } from "./resources/original_configs";

export const loadInitialConfig = () => 
{
    let under = originalUnder;
    let right = originalRight;
    let beaker = originalBeaker;
    let commons = originalCommons;
    if (typeof(Storage)!=="undefined") 
    {
        let res: string | null = null;
        if (res=localStorage.getItem("config.under")) under = res;
        if (res=localStorage.getItem("config.right")) right = res;
        if (res=localStorage.getItem("config.beaker")) beaker = res;
        if (res=localStorage.getItem("config.commons")) commons = res;
    } 
    return { under,right,beaker,commons };
};

export const saveConfig = (type: TLogoType | "commons",config: string) =>
{
    if (typeof(Storage)!=="undefined")
    {
        localStorage.setItem(`config.${type}`,config);
    }
}