import yaml from "js-yaml";
import { TAnimationDefinition } from "@prethora/alchemy_labs_logo_svg_demo_animation_manager";
import { TLogoType } from "./components/GifMaker/GifMaker";

const validateNumber = (value: number,name: string,zeroAllowed = false,negativeAllowed = false,commons = false) => 
{
    const nameSegment = `'${name}'${commons?" (commons)":""}`;
    if (typeof (value)!=="number") return `${nameSegment} is expected to be a number`;
    if ((value<0) && (!negativeAllowed))
    {
        if (zeroAllowed)
        {
            return `${nameSegment} cannot be negative`;
        }
        else
        {
            return `${nameSegment} must be greater than zero`;
        }
    }
    else if ((value===0) && (!zeroAllowed))
    {
        if (negativeAllowed)
        {
            return `${nameSegment} must be less or greater than zero, but not zero`;
        }
        else
        {
            return `${nameSegment} must be greater than zero`;
        }
    }
};

export function createDefinition(config: string,commons: string,type: TLogoType): TAnimationDefinition | string
{
    try
    {
        commons = commons.split("\n").map((line) => 
        {
            const res = /^\s*backgroundColor\s*:\s*(#[\s\S]*)$/.exec(line);
            if (res)
            {
                return `backgroundColor: "${res[1]}"`;
            }
            else
            {
                return line;
            }
        }).join("\n");
        const definition = yaml.load(config+"\n"+commons) as TAnimationDefinition;

        const validateResolution = (resolution: TAnimationDefinition["resolution"]) => 
        {
            if (!resolution) return "The 'resolution' object is required";
            let res: string | undefined;
            if ((res=validateNumber(resolution.height,"resolution.height"))!==undefined) return res;
        };
        const validateMargins = (margins: TAnimationDefinition["margins"]) => 
        {
            if (!margins) return "The 'margins' object is required";
            let res: string | undefined;
            if ((res=validateNumber(margins.left,"margins.left",true))!==undefined) return res;
            if ((res=validateNumber(margins.right,"margins.right",true))!==undefined) return res;
            if ((res=validateNumber(margins.top,"margins.top",true))!==undefined) return res;
            if ((res=validateNumber(margins.bottom,"margins.bottom",true))!==undefined) return res;
            if ((res=validateNumber(margins.gap,"margins.gap",true))!==undefined) return res;
        };
        const validateFlask = (flask: TAnimationDefinition["flask"]) => 
        {
            if (!flask) return "The 'flask' object is required";
            let res: string | undefined;
            if ((res=validateNumber(flask.horizontalOffset,"flask.horizontalOffset",true,true))!==undefined) return res;
        };
        const validateText = (text: TAnimationDefinition["text"]) => 
        {
            if (!text) return "The 'text' object is required";
            let res: string | undefined;
            if ((res=validateNumber(text.verticalOffset,"text.verticalOffset",true,true))!==undefined) return res;
        };
        const validateBackgroundColor = (backgroundColor: string) => 
        {
            if (typeof backgroundColor!=="string") return "'backgroundColor' (commons) is expected to be a string";
            if (backgroundColor.trim()==="") return "'backgroundColor' (commons) cannot be empty";
        };
        const validateBubble = (bubbles: TAnimationDefinition["bubbles"],index: number) => 
        {
            const bubble = bubbles[index];
            if (!bubble) return `The 'bubbles[${index}]' (commons) object is required`;
            let res: string | undefined;
            if (!bubble.exitPosition) return `The 'bubbles[${index}].exitPosition' (commons) object is required`;
            if ((res=validateNumber(bubble.exitPosition.x,`bubbles[${index}].exitPosition.x`,true,true,true))!==undefined) return res;
            if ((res=validateNumber(bubble.exitPosition.y,`bubbles[${index}].exitPosition.y`,true,true,true))!==undefined) return res;
            if ((res=validateNumber(bubble.radius,`bubbles[${index}].radius`,true,false,false))!==undefined) return res;
            if ((res=validateNumber(bubble.thickness,`bubbles[${index}].thickness`,true,false,false))!==undefined) return res;
        };
        const validateBubbles = (bubbles: TAnimationDefinition["bubbles"]) => 
        {
            if (!Array.isArray(bubbles)) return "The 'bubbles' (commons) array is required";
            if (bubbles.length===0) return "The 'bubbles' (commons) array must contain at least one bubble";
            let res: string | undefined;            
            for(let i=0;i<bubbles.length;i++)
            {
                if ((res=validateBubble(bubbles,i))!==undefined) return res;    
            }           
        };
        const validateDurations = (durations: TAnimationDefinition["durations"]) => 
        {
            if (!durations) return "The 'durations' (commons) object is required";
            let res: string | undefined;
            if ((res=validateNumber(durations.beforeBurst,"durations.beforeBurst",false,false,true))!==undefined) return res;
            if ((res=validateNumber(durations.between,"durations.between",true,false,true))!==undefined) return res;
            if ((res=validateNumber(durations.burst,"durations.burst",false,false,true))!==undefined) return res;
            if ((res=validateNumber(durations.initial,"durations.initial",true,false,true))!==undefined) return res;
        };
        const validateBubblePath = (bubblePath: TAnimationDefinition["bubblePath"]) => 
        {
            if (!bubblePath) return "The 'bubblePath' (commons) object is required";
            if (!bubblePath.wave) return "The 'bubblePath.wave' (commons) object is required";
            let res: string | undefined;
            if ((res=validateNumber(bubblePath.wave.frequency,"bubblePath.wave.frequency",false,false,true))!==undefined) return res;
            if ((res=validateNumber(bubblePath.wave.amplitude,"bubblePath.wave.amplitude",false,false,true))!==undefined) return res;
            if ((res=validateNumber(bubblePath.height,"bubblePath.height",false,false,true))!==undefined) return res;
            if (!bubblePath.burst) return "The 'bubblePath.burst' (commons) object is required";
            if ((res=validateNumber(bubblePath.burst.particleCount,"bubblePath.burst.particleCount",false,false,true))!==undefined) return res;
            if ((res=validateNumber(bubblePath.burst.radius,"bubblePath.burst.radius",false,false,true))!==undefined) return res;
        };

        if (type==="under")
        {
            definition.orientation = "under";
            definition.text = {
                verticalOffset: 0,
                scale: 1
            };
        }
        else if (type==="right")
        {
            definition.orientation = "right";
            definition.flask = {
                horizontalOffset: 0
            };        
        }    
        else if (type==="beaker")
        {
            definition.orientation = "under";
            definition.margins.gap = 0;
            definition.margins.left+= 50;
            definition.margins.right+= 50;
            definition.flask = {
                horizontalOffset: 0
            };        
            definition.text = {
                verticalOffset: 0,
                scale: 0
            };
        }
        let res: string | undefined;
        if ((res=validateResolution(definition.resolution))!==undefined) return res;
        if ((res=validateMargins(definition.margins))!==undefined) return res;
        if ((res=validateFlask(definition.flask))!==undefined) return res;
        if ((res=validateText(definition.text))!==undefined) return res;        

        if ((res=validateBackgroundColor(definition.backgroundColor))!==undefined) return res;
        if ((res=validateDurations(definition.durations))!==undefined) return res;
        if ((res=validateBubblePath(definition.bubblePath))!==undefined) return res;

        return definition;    
    }
    catch(err)
    {
        return "Invalid YAML input, make sure configuration format is valid";
    }
}
