import * as React from "react";
import { useState } from "react";
import styles from "./ErrorMessage.scss";

interface TProp
{
    message?: string;
    show: boolean;
}

export const useErrorMessage = () => 
{
    const [message,setMessage] = useState("");
    const [show,setShow] = useState(false);
    let lastTimeout = 0;
    const ret = {
        props: {
            message,show
        },
        showErrorMessage: (message: string,timeout = -1) => 
        {
            clearTimeout(lastTimeout);lastTimeout = 0;
            setMessage(message);
            setShow(true);
            if (timeout>0)
            {
                lastTimeout = setTimeout(ret.closeErrorMessage,timeout);
            }
        },
        closeErrorMessage: () => setShow(false)
    };
    return ret;
};

export function ErrorMessage({message,show: shouldShow}: TProp)
{
    const [counter,setCounter] = useState(0);
    const { error,show } = styles;

    return (
        <div className={error+" "+(shouldShow?show:"")}>
            <div><span>{message}</span></div>            
        </div>
    );
}
