import * as React from "react";
import { useState } from "react";
import styles from "./Tabs.scss";

interface TProp
{
    selectedIndex: number;
    onSelect?: (index: number) => void;
}

export function Tabs({selectedIndex,onSelect}: TProp)
{
    const { tab,tab0,tab1,tab2,container,selected,selected0,selected2 } = styles;

    const clickHandler = (index: number) => 
    {
        if (onSelect) onSelect(index);
    };

    return (
        <div className={container+" "+((selectedIndex===0)?selected0:((selectedIndex===2)?selected2:""))}>
            <div className={tab+" "+tab0+" "+((selectedIndex===0)?selected:"")} onClick={() => clickHandler(0)}>Under</div>
            <div className={tab+" "+tab1+" "+((selectedIndex===1)?selected:"")} onClick={() => clickHandler(1)}>Right</div>
            <div className={tab+" "+tab2+" "+((selectedIndex===2)?selected:"")} onClick={() => clickHandler(2)}>Beaker</div>
        </div>
    );
}
