import * as React from "react";
import { useState,useEffect } from "react";
import { TLogoType } from "../GifMaker";
import styles from "./RightPanel.scss";
import { SvgDisplay } from "./SvgDisplay/SvgDisplay";

interface TProp
{
    urls: {
        under?: string;
        right?: string;
        beaker?: string;
    };
    type: TLogoType;
}

export function RightPanel({urls,type}: TProp)
{
    useEffect(() =>
    {
    },[]);

    return (
        <>
            <SvgDisplay url={urls.under} show={type=="under"} name="under" />
            <SvgDisplay url={urls.right} show={type=="right"} name="right" />
            <SvgDisplay url={urls.beaker} show={type=="beaker"} name="beaker" />
        </>
    );
}
