export const styler = (styles: {[key: string]: string}) => 
{    
    return (...entries: (string | [string,boolean | undefined])[]) => 
    {
        return entries.map((entry) => 
        {
            if (typeof entry==="string")
            {
                return styles[entry];
            }
            else
            {
                return styles[entry[1]?entry[0]:""];
            }
        }).join(" ");
    };
};

export const handler = (eventHandler?: () => void) => () => 
{
    if (eventHandler) eventHandler();
};