import * as React from "react";
import { useState,useEffect,useRef } from "react";
import styles from "./SvgDisplay.scss";

interface TProp
{
    url?: string;
    show: boolean;
    name?: string;
}

export function SvgDisplay({url,show: shouldShow,name}: TProp)
{
    const [active,setActive] = useState(0);
    const [url0,setUrl0] = useState<string | undefined>();
    const [url1,setUrl1] = useState<string | undefined>();
    const permRef = useRef<{active: number,shouldShow: boolean,lastUrl?: string}>({active,shouldShow});
    permRef.current.active = active;
    permRef.current.shouldShow = shouldShow;

    const nextRunRef = useRef<(() => void) | null>(null);

    useEffect(() => 
    {        
        if (url)
        {
            const { active,lastUrl,shouldShow } = permRef.current;

            if (shouldShow)
            {
                if (lastUrl)
                {
                    nextRunRef.current = () => 
                    { 
                        setActive(3-active);
                    };        
                    ((active===0)?setUrl1:setUrl0)(url);
                }
                else
                {
                    ((active===0)?setUrl0:setUrl1)(url);    
                }
            }
            else
            {
                ((active===0)?setUrl0:setUrl1)(url);
            }

            if (permRef.current.lastUrl)
            {
                URL.revokeObjectURL(permRef.current.lastUrl);
            }

            permRef.current.lastUrl = url;
        }
    },[url]);

    const nextLoad = () => 
    {
        if (nextRunRef.current)
        {
            const __ = nextRunRef.current;
            nextRunRef.current = null;
            __();            
        }        
    };
    const { container,vertiWrapper,horizWrapper,show } = styles;

    const transitionEndHandler = () => 
    {
        const cActive = permRef.current.active;    
        if (cActive>1) setActive(cActive-2);
    };

    return (
        <>
            <div className={horizWrapper+" "+((shouldShow && (active===0))?show:"")} 
                onTransitionEnd={(e) => { if (e.propertyName==="opacity") transitionEndHandler(); }}
            >
                <div className={vertiWrapper}>
                    <div className={container}>{url0?<object data={url0} type="image/svg+xml" onLoad={nextLoad} />:null}</div>
                </div>
            </div>
            <div className={horizWrapper+" "+((shouldShow && (active===1))?show:"")}
                onTransitionEnd={(e) => { if (e.propertyName==="opacity") transitionEndHandler(); }}
            >
                <div className={vertiWrapper}>
                    <div className={container}>{url1?<object data={url1} type="image/svg+xml" onLoad={nextLoad} />:null}</div>
                </div>
            </div>
        </>
    );
}
